import React, {PropTypes} from 'react';
import './app.scss';
import { Grid, Row, Col, Jumbotron, ListGroup, ListGroupItem, Checkbox, Radio,
  FormGroup, ControlLabel, FormControl, Button, HelpBlock, PageHeader } from 'react-bootstrap';

class App extends React.Component {
  state = {
    drawerActive: false,
    drawerPinned: true,
    sidebarPinned: false
  };

  render() {
    function FieldGroup({ id, label, help, ...props }) {
      return (
        <FormGroup controlId={id}>
          <ControlLabel>{label}</ControlLabel>
          <FormControl {...props} />
          {help && <HelpBlock>{help}</HelpBlock>}
        </FormGroup>
      );
    }

    return (
      <div>
        <PageHeader>
          <Grid>
            <Row>
              <Col md={12}>
                Админка 2.0
              </Col>
            </Row>
          </Grid>
        </PageHeader>
        <Grid>
          <Row className="show-grid">
            <Col md={3}>
              <ListGroup>
                <ListGroupItem href="#">Начало</ListGroupItem>
                <ListGroupItem href="#">Account</ListGroupItem>
                <ListGroupItem href="#">Adv</ListGroupItem>
                <ListGroupItem href="#">Auth</ListGroupItem>
                <ListGroupItem href="#">Djcelery</ListGroupItem>
                <ListGroupItem href="#">Sites</ListGroupItem>
                <ListGroupItem href="#">Vkproxy</ListGroupItem>
                <ListGroupItem href="#">Object state log</ListGroupItem>
                <ListGroupItem href="#">Sync log</ListGroupItem>
                <ListGroupItem href="#">Начало</ListGroupItem>
                <ListGroupItem href="#">Account</ListGroupItem>
                <ListGroupItem href="#">Adv</ListGroupItem>
                <ListGroupItem href="#">Auth</ListGroupItem>
                <ListGroupItem href="#">Djcelery</ListGroupItem>
                <ListGroupItem href="#">Sites</ListGroupItem>
                <ListGroupItem href="#">Vkproxy</ListGroupItem>
                <ListGroupItem href="#">Object state log</ListGroupItem>
                <ListGroupItem href="#">Sync log</ListGroupItem>
                <ListGroupItem href="#">Начало</ListGroupItem>
                <ListGroupItem href="#">Account</ListGroupItem>
                <ListGroupItem href="#">Adv</ListGroupItem>
                <ListGroupItem href="#">Auth</ListGroupItem>
                <ListGroupItem href="#">Djcelery</ListGroupItem>
                <ListGroupItem href="#">Sites</ListGroupItem>
                <ListGroupItem href="#">Vkproxy</ListGroupItem>
                <ListGroupItem href="#">Object state log</ListGroupItem>
                <ListGroupItem href="#">Sync log</ListGroupItem>
              </ListGroup>
            </Col>
            <Col md={9}>
              <Jumbotron>
                <h2>Добро пожаловать, бла бла бла</h2>
                <p>Тест форм</p>
              </Jumbotron>

              <form>
                <FieldGroup
                  id="formControlsText"
                  type="text"
                  label="Text"
                  placeholder="Enter text"
                />
                <FieldGroup
                  id="formControlsEmail"
                  type="email"
                  label="Email address"
                  placeholder="Enter email"
                />
                <FieldGroup
                  id="formControlsPassword"
                  label="Password"
                  type="password"
                />
                <FieldGroup
                  id="formControlsFile"
                  type="file"
                  label="File"
                  help="Example block-level help text here."
                />

                <Checkbox checked readOnly>
                  Checkbox
                </Checkbox>
                <Radio checked readOnly>
                  Radio
                </Radio>

                <FormGroup>
                  <Checkbox inline>
                    1
                  </Checkbox>
                  {' '}
                  <Checkbox inline>
                    2
                  </Checkbox>
                  {' '}
                  <Checkbox inline>
                    3
                  </Checkbox>
                </FormGroup>
                <FormGroup>
                  <Radio inline>
                    1
                  </Radio>
                  {' '}
                  <Radio inline>
                    2
                  </Radio>
                  {' '}
                  <Radio inline>
                    3
                  </Radio>
                </FormGroup>

                <FormGroup controlId="formControlsSelect">
                  <ControlLabel>Select</ControlLabel>
                  <FormControl componentClass="select" placeholder="select">
                    <option value="select">select</option>
                    <option value="other">...</option>
                  </FormControl>
                </FormGroup>
                <FormGroup controlId="formControlsSelectMultiple">
                  <ControlLabel>Multiple select</ControlLabel>
                  <FormControl componentClass="select" multiple>
                    <option value="select">select (multiple)</option>
                    <option value="other">...</option>
                  </FormControl>
                </FormGroup>

                <FormGroup controlId="formControlsTextarea">
                  <ControlLabel>Textarea</ControlLabel>
                  <FormControl componentClass="textarea" placeholder="textarea" />
                </FormGroup>

                <FormGroup>
                  <ControlLabel>Static text</ControlLabel>
                  <FormControl.Static>
                    email@example.com
                  </FormControl.Static>
                </FormGroup>

                <Button type="submit">
                  Submit
                </Button>
              </form>
            </Col>
          </Row>
        </Grid>
      </div>
    );
  }
}

App.propTypes = {
  children: PropTypes.object.isRequired
};

export default App;



